.cases-item__text { line-height: 1.75; }

.cases-item__text p:not(:first-child) { margin-top: 1rem; }

.cases-item__author { display: flex; align-items: center; }

.cases-item__photo { border-radius: 50%; overflow: hidden; margin-right: 30px; }

.cases-item__photo img { display: flex; width: 100%; }

.cases-item__img { display: flex; align-items: center; justify-content: center; }

.cases-item__img img { display: block; max-width: 100%; }

.steps { counter-reset: steps; }

.steps__item:not(:first-child) { margin-top: 1.5rem; }

.steps-item__left { position: relative; padding: 80px 50px; height: 100%; background-color: #f0f0f0; display: flex; align-items: center; }

@media only screen and (max-width: 1590px) { .steps-item__left { padding: 60px 30px; } }

.steps-item__name { font-size: 20px; line-height: 1.6; font-weight: 300; }

.steps-item__name:after { counter-increment: steps; content: counter(steps); position: absolute; color: #fff; font-size: 80px; font-weight: 300; position: absolute; bottom: -10px; right: 40px; line-height: 1; }

@media only screen and (max-width: 1259px) { .steps-item__name:after { font-size: 60px; bottom: -7px; } }

.steps-item:last-child .steps-item__name { color: #0d9838; }

.steps-item__num { color: #fff; font-size: 80px; font-weight: 300; position: absolute; bottom: -10px; right: 40px; line-height: 1; }

@media only screen and (max-width: 1259px) { .steps-item__num { font-size: 60px; } }

.steps-item__right { padding: 80px 66px; height: 100%; display: flex; align-items: center; justify-content: center; background-color: #fafafa; }

@media only screen and (max-width: 1590px) { .steps-item__right { padding: 60px 45px; } }

@media only screen and (max-width: 1259px) { .steps-item__right { padding: 60px 30px; } }

.steps-item__text { line-height: 1.625; }

.footer { padding: 60px 0; }

.footer__wrap { display: flex; flex-wrap: wrap; margin-right: -15px; margin-left: -15px; }

.footer__left, .footer__right, .footer__center, .footer__col, .footer__item { width: 100%; padding-right: 15px; padding-left: 15px; }

.footer__left { flex: 0 0 25%; max-width: 25%; }

@media only screen and (max-width: 1259px) { .footer__left { flex: 0 0 33.33333%; max-width: 33.33333%; } }

@media only screen and (max-width: 767px) { .footer__left { flex: 0 0 100%; max-width: 100%; } }

.footer__center { flex: 0 0 33.33333%; max-width: 33.33333%; }

@media only screen and (max-width: 1023px) { .footer__center { flex: 0 0 66.66667%; max-width: 66.66667%; } }

@media only screen and (max-width: 767px) { .footer__center { flex: 0 0 100%; max-width: 100%; margin-top: 1.5rem; } }

.footer__right { flex: 0 0 41.66667%; max-width: 41.66667%; }

@media only screen and (max-width: 1259px) { .footer__right { flex: 0 0 33.33333%; max-width: 33.33333%; } }

@media only screen and (max-width: 1023px) { .footer__right { flex: 0 0 100%; max-width: 100%; margin-top: 10px; } }

@media only screen and (max-width: 767px) { .footer__right { margin-top: 1.5rem; } }

.footer__col { flex: 0 0 41.66667%; max-width: 41.66667%; }

@media only screen and (max-width: 1259px) { .footer__col { flex: 0 0 100%; max-width: 100%; order: 10; text-align: center; } }

@media only screen and (max-width: 1023px) { .footer__col { flex: 0 0 50%; max-width: 50%; order: 1; text-align-last: left; } }

@media only screen and (max-width: 599px) { .footer__col { flex: 0 0 100%; max-width: 100%; margin-bottom: 3rem; } }

.footer__col_xl { flex: 0 0 58.33333%; max-width: 58.33333%; }

@media only screen and (max-width: 1259px) { .footer__col_xl { flex: 0 0 100%; max-width: 100%; } }

@media only screen and (max-width: 599px) { .footer__col_xl { margin-bottom: 0; } }

@media only screen and (max-width: 1259px) { .footer__col_order { order: 1; margin-bottom: 1rem; } }

.footer__founder { font-size: 24px; line-height: 1.5; margin-bottom: 1rem; }

.footer__email { font-size: 14px; opacity: 0.4; transition: opacity .5s ease, color .5s ease; }

.no-touch .footer__email:hover { opacity: 1; color: #0d9838; }

.footer__item { flex: 0 0 50%; max-width: 50%; }

@media only screen and (max-width: 599px) { .footer__item { flex: 0 0 100%; max-width: 100%; } }

@media only screen and (max-width: 599px) { .footer__item:not(:last-child) { margin-bottom: 1.5rem; } }

.footer__tel { white-space: nowrap; font-size: 20px; }

.no-touch .footer__tel:hover { color: #333333; }

.footer__worktime { font-size: 13px; text-transform: uppercase; letter-spacing: 1.12px; }

.footer-menu__header { margin-bottom: 13px; font-size: 14px; color: #0d9838; line-height: 1.85; letter-spacing: 0.75px; text-transform: uppercase; }

.footer-menu__item:not(:first-child) { margin-top: 20px; }
