.about {
    padding: 100px 0;
    @include sm {
        padding: 60px 0; }
    &__wrap {
        background-color: $block-bg;
        padding: 78px 85px 0 67px;
        @include md {
            padding: 70px 80px 0; }
        @include sm {
            padding: 40px 40px 0; }
        @include xs {
            margin-left: -15px;
            width: calc(100% + 30px);
            padding: 40px 30px 0; } }
    &__content {
        display: flex;
        justify-content: center;
        flex-wrap: wrap; }
    &__left {
        flex: 0 0 41.66667%;
        max-width: 41.66667%;
        width: 100%;
        padding-right: 15px;
        align-self: flex-end;
        @include lg {
            flex: 0 0 66.66667%;
            max-width: calc(66.66667% - 30px);
            order: 10;
            padding-right: 0; }
        @include sm {
            flex: 0 0 100%;
            max-width: 100%; } }
    &__right {
        flex: 0 0 58.33333%;
        max-width: 58.33333%;
        width: 100%;
        padding-left: 85px;
        padding-bottom: 60px;
        @include lg {
            flex: 0 0 100%;
            max-width: 100%;
            padding-left: 0; } }
    &__img {
        img {
            display: block;
            max-width: 100%;
            width: 100%; } }
    &__text {
        text-align: justify;
        margin-bottom: 40px;
        p {
            position: relative;
            line-height: 1.85;
            &:not(:last-child) {
                margin-bottom: 27px; }
            &:last-child {
                margin-top: 56px;
                &:before {
                    position: absolute;
                    content: "";
                    top: -26px;
                    left: 0;
                    width: 37px;
                    height: 1px;
                    background-color: $main; } }
            &:first-child {
                margin-top: 0;
                &:before {
                    display: none; } } } } }

