.services {
    padding: 100px 0 70px;
    position: relative;
    z-index: 5;
    @include xs {
        padding: 80px 0 50px; }
    &__list {
        [class*='col'] {
            margin-bottom: 1.5rem; } } }

.services-controls {
    margin: -180px 0 100px;
    @include md {
        margin-top: 0; }
    @include xs {
        margin-bottom: 80px; }
    &__row {
        justify-content: flex-end;
        @include md {
            justify-content: center; }
        .col-3 {
            &:not(:last-child) {
                @include md {
                    margin-bottom: 1.5rem; } } } }
    &__item {
        padding: 50px;
        display: flex;
        align-items: center;
        height: 100%;
        color: $white;
        background-color: #222;
        transition: background-color .3s ease;
        @include hover {
            background-color: $main;
            color: $white;
            border-color: $white;
            .services-controls__name span {
                border-bottom-color: $white; } }
        &.is-active {
            background-color: $main;
            color: $white;
            border-color: $white;
            .services-controls__name span {
                border-bottom-color: $white; } } }

    &__name {
        display: block;
        max-width: 130px;
        span {
            display: inline;
            border-bottom: 1px solid transparent;
            transition: border-color .3s ease; } } }

.services-item {
    display: flex;
    // align-items: center
    justify-content: center;
    flex-direction: column;
    padding: 40px 60px;
    height: 100%;
    min-height: 360px;
    background-color: #252628;
    transition: background-color .3s ease;
    color: $white;
    @include hover {
        background-color: $main;
        color: $white;
        .services-item__continue {
            max-height: 80px; }
        .services-item__more {
            opacity: 1;
            transform: translateX(0); } }
    @include md {
        min-height: 280px; }
    &_low {
        min-height: 210px;
        cursor: pointer;
        @include md {
            padding: 40px 35px;
            min-height: 210px; } }
    &__name,
    &__continue,
    &__more {
        display: block; }
    &__continue {
        height: 80px;
        max-height: 0;
        position: relative;
        overflow: hidden;
        transition: max-height .3s ease; }
    &__more {
        position: absolute;
        left: 0;
        bottom: 0;
        opacity: 0;
        transform: translateX(-40px);
        transition: transform .3s ease, opacity .3s ease;
        transition-delay: .2s;
        color: rgba($white,.3);
        .arrow-more {
            color: rgba($white, .3); }
        .arrow-more__icon {
            color: #c6c6c6; } } }

.services-item-big {
    @include hover {
        .services-item {
            background-color: $main;
            color: $white;
            .services-item__continue {
                max-height: 80px; }
            .services-item__more {
                opacity: 1;
                transform: translateX(0); } } }
    .row {
        margin: 0; }
    [class*='col'] {
        padding: 0;
        margin: 0; }
    &__img {
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: center; } } }

.services-list {
    padding-bottom: 100px;
    @include sm {
        padding-bottom: 60px; }
    &__col {
        margin-bottom: 1.5rem; } }

.services-callback {
    padding: 60px;
    background-color: $block-bg;
    height: 100%;
    display: flex;
    align-items: center;
    @include md {
        padding: 60px 40px; }
    @include xs {
        padding: 60px 30px; }
    &__row {
        align-items: center;
        margin: 0; }
    &__col {
        padding: 0;
        &:not(:first-child) {
            padding-left: 1rem;
            @include sm {
                padding-left: 0;
                margin-top: 1.5rem; } } }
    &__text {
        font-size: 18px;
        span {
            color: $main;
            display: block;
            @include sm {
                display: inline; } } } }
