.why {
    padding-bottom: 100px;
    &__title {
        span {
            display: block;
            @include md {
                display: inline; } } }
    &__wrap {
        padding: 100px;
        background-color: $block-bg;
        @include r(1590) {
            padding: 60px; }
        @include sm {
            padding: 40px; }
        @include xs {
            padding: 30px; } }
    &__text {
        line-height: 1.75;
        p {
            position: relative;
            &:not(:first-child) {
                margin-top: 30px; }
            &:last-child {
                margin-top: 82px;
                &:before {
                    position: absolute;
                    content: "";
                    top: -41px;
                    left: 0;
                    width: 56px;
                    height: 2px;
                    background-color: $main; } }
            &:first-child {
                margin-top: 0;
                &:before {
                    display: none; } } } } }
