.footer {
    padding: 60px 0;
    &__wrap {
        display: flex;
        flex-wrap: wrap;
        margin-right: -15px;
        margin-left: -15px; }
    &__left,
    &__right,
    &__center,
    &__col,
    &__item {
        width: 100%;
        padding-right: 15px;
        padding-left: 15px; }
    &__left {
        flex: 0 0 25%;
        max-width: 25%;
        @include lg {
            flex: 0 0 33.33333%;
            max-width: 33.33333%; }
        @include sm {
            flex: 0 0 100%;
            max-width: 100%; } }
    &__center {
        flex: 0 0 33.33333%;
        max-width: 33.33333%;
        @include md {
            flex: 0 0 66.66667%;
            max-width: 66.66667%; }
        @include sm {
            flex: 0 0 100%;
            max-width: 100%;
            margin-top: 1.5rem; } }
    &__right {
        flex: 0 0 41.66667%;
        max-width: 41.66667%;
        @include lg {
            flex: 0 0 33.33333%;
            max-width: 33.33333%; }
        @include md {
            flex: 0 0 100%;
            max-width: 100%;
            margin-top: 10px; }
        @include sm {
            margin-top: 1.5rem; } }
    &__col {
        flex: 0 0 41.66667%;
        max-width: 41.66667%;
        @include lg {
            flex: 0 0 100%;
            max-width: 100%;
            order: 10;
            text-align: center; }
        @include md {
            flex: 0 0 50%;
            max-width: 50%;
            order: 1;
            text-align-last: left; }
        @include xs {
            flex: 0 0 100%;
            max-width: 100%;
            margin-bottom: 3rem; }
        &_xl {
            flex: 0 0 58.33333%;
            max-width: 58.33333%;
            @include lg {
                flex: 0 0 100%;
                max-width: 100%; }
            @include xs {
                margin-bottom: 0; } }
        &_order {
            @include lg {
                order: 1;
                margin-bottom: 1rem; } } }
    &__founder {
        font-size: 24px;
        line-height: 1.5;
        margin-bottom: 1rem; }
    &__email {
        font-size: 14px;
        opacity: 0.4;
        transition: opacity .5s ease, color .5s ease;
        @include hover {
            opacity: 1;
            color: $main; } }
    &__item {
        flex: 0 0 50%;
        max-width: 50%;
        @include xs {
            flex: 0 0 100%;
            max-width: 100%; }
        &:not(:last-child) {
            @include xs {
                margin-bottom: 1.5rem; } } }
    &__tel {
        white-space: nowrap;
        font-size: 20px;
        @include hover {
            color: $dark; } }
    &__worktime {
        font-size: 13px;
        text-transform: uppercase;
        letter-spacing: 1.12px; } }

.footer-menu {
    &__header {
        margin-bottom: 13px;
        font-size: 14px;
        color: $main;
        line-height: 1.85;
        letter-spacing: 0.75px;
        text-transform: uppercase; }
    &__item {
        &:not(:first-child) {
            margin-top: 20px; } }
    &__link {} }
