.cases {
    padding: 100px 0;
    @include sm {
        padding: 60px 0; }
    &__content {
        display: flex;
        justify-content: center;
        position: relative; }
    &__list {
        width: 100%;
        max-width: 83.33333%;
        @include md {
            max-width: 100%; }
        .slick-dots {
            bottom: -32px;
            li {
                &.slick-active {
                    button {
                        &:before {
                            background-color: $main; } } }
                @include hover {
                    button {
                        &:before {
                            background-color: $main; } } }
                button {
                    &:before {
                        background-color: rgba($main,.3); } } } } }
