    //     position: relative
    //     &:before
    //         position: absolute
    //         top: 50%
    //         left: 50%
 }    //         transform: translate(-50%, -50%)
.arrow-prev,
.arrow-next {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background-color: rgba($main,.5);
    color: $white;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: background-color .3s ease;
    z-index: 10;
    @include hover {
        background-color: $main;
        .arrow-more__icon-inner {
            max-width: calc(100% - 5px); } }
    @include xs {
        display: none; }
    .arrow-more {
        height: 12px;
        &__icon {
            color: $white;
            width: 15px;
            margin: 0; } } }
.arrow-prev {
    left: -40px;
    .arrow-more {
        transform: rotate(180deg); } }
.arrow-next {
    right: -40px; }

.cases-item {
    padding: 100px;
    padding-bottom: 60px;
    background-color: $block-bg;
    @include r(1590) {
        padding: 70px;
        padding-bottom: 40px; }
    @include md {
        padding: 55px;
        padding-bottom: 35px; }
    @include xs {
        padding: 40px 25px 30px; }
    &__head {
        font-size: 16px;
        font-weight: 500;
        margin-bottom: 10px; }
    &__title {
        font-size: 32px;
        font-weight: 300;
        color: $main;
        @include xs {
            font-size: 20px; } }
    &__block {
        margin-bottom: 50px;
        @include xs {
            margin-bottom: 20px; } }
    &__text {
        line-height: 1.75;
        p {
            &:not(:first-child) {
                margin-top: 1rem; } } }
    &__author {
        display: flex;
        align-items: center; }
    &__photo {
        border-radius: 50%;
        overflow: hidden;
        margin-right: 30px;
        img {
            display: flex;
            width: 100%; } }
    &__img {
        display: flex;
        align-items: center;
        justify-content: center;
        img {
            display: block;
            max-width: 100%; } } }
