.section-articles {
    &__title {
        margin-bottom: 50px; }
    &__wrap {
        display: flex;
        flex-wrap: wrap;
        margin-right: -15px;
        margin-left: -15px; }
    &__col {
        flex: 0 0 33.33333%;
        max-width: 33.33333%;
        width: 100%;
        padding-right: 15px;
        padding-left: 15px;
        margin-bottom: 90px;
        @include md {
            flex: 0 0 50%;
            max-width: 50%; }
        @include sm {
            flex: 0 0 100%;
            max-width: 100%; } } }

.article-card {
    display: block;
    &_horizontal {
        display: flex;
        width: 100%;
        padding: 30px 15px;
        background-color: $card-bg;
        @include md {
            flex-direction: column; } }
    @include hover {
        color: $dark;
        .article-card__title {
            color: $main; }
        .arrow-more__icon-inner {
            max-width: 70px; } }
    &__img {
        position: relative;
        display: block;
        height: 208px;
        overflow: hidden;
        @include md {
            .article-card_horizontal & {
                height: auto; } }
        @include sm {
            max-height: 185px;
            .article-card_horizontal & {
                max-height: inherit; } }
        @include hover {
            img {
                transform: scale(1); } }
        &:before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            background-color: $main;
            opacity: 0.3;
            z-index: 1; }
        img {
            display: block;
            width: 100%;
            transform: scale(1.03);
            transition: transform .5s ease-in-out;
            height: 100%;
            object-fit: cover;
            object-position: center; } }
    &__image-wrapper {
        .article-card_horizontal & {
            @include flex-size(33.333%);
            padding: 0 15px;
            @include md {
                margin-bottom: 1.5rem;
                flex-basis: auto; } } }
    &__text-wrapper {
        .article-card_horizontal & {
            display: flex;
            flex-direction: column;
            justify-content: center;
            flex-grow: 1;
            padding: 0 15px 0 65px;
            @include md {
                padding: 0 15px; } } }
    &__date {
        display: block;
        font-size: 12px;
        color: rgba($dark,.4);
        letter-spacing: 0.63px;
        text-transform: uppercase;
        margin-top: 30px;
        .article-card_horizontal & {
            margin-top: 0; } }
    &__title {
        display: block;
        margin-top: 12px;
        transition: color .3s; }
    &__text {
        display: block;
        margin-top: 20px;
        font-size: 14px;
        line-height: 1.7;
        height: 45px;
        overflow: hidden; }
    &__more {
        display: block;
        margin-top: 30px;
        opacity: 0.8;
        align-self: end; } }
