.section-contacts {
    position: relative;
    // padding-left: 100px
    &__map {
        width: 100%;
        height: 658px;
        @include sm {
            height: 450px; } }
    &__info {
        position: absolute;
        top: 0;
        left: 0;
        min-width: 30%;
        @include sm {
            position: relative;
            min-width: auto;
            width: 100%; } } }
.contacts-info {
    padding: 100px;
    background-color: #29292a;
    color: $white;
    z-index: 10;
    @include md {
        padding: 50px 30px; }
    &__title {
        margin-bottom: 1rem; }
    address {
        font-style: normal; }
    &__phone {
        margin-top: 1rem;
        a {
            color: $main;
            font-size: 24px;
            font-weight: 300;
            @include hover {
                color: darken($main,10%); } } }
    &__email {
        margin-top: 0.5rem;
        a {
            color: $white;
            @include hover {
                color: $white; } } } }

.contacts {
    padding-top: 150px;
    background-color: #202020;
    @include sm {
        padding-top: 110px; }
    &__row {
        align-items: flex-end; }
    .divider {
        margin: 0 0 40px; }
    &__content {
        color: $white; }
    address {
        font-style: normal;
        margin-bottom: 1rem;
        color: #9a9999; }
    &__phone {
        display: block;
        font-size: 24px;
        letter-spacing: 0.6px;
        line-height: 1.167;
        color: $main;
        white-space: nowrap;
        &-white {
            white-space: nowrap;
            color: $white;
            @include hover {
                color: $white; } } }
    &__email {
        display: block;
        color: #999;
        font-size: 14px;
        margin-top: 1rem;
        @include hover {
            color: #999; } }
    &__divider {
        width: 100%;
        height: 1px;
        background-color: #272626;
        margin: 40px 0; }
    &__small {
        font-size: 14px;
        line-height: 2rem;
        margin-bottom: 3rem; }
    &__link {
        color: $main;
        border-bottom: 1px solid;
        @include hover {
            color: darken($main,10%); } }
    &__map {
        width: 100%;
        height: 400px;
        @include sm {
 } } }            // height: 450px

